@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');


body {
  margin: 0;
  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7f6ff;
  font-family: "Inter", sans-serif;
}

.wrapper{
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.4);
  border-radius: 20px;
  max-width: 1280px;
  margin: 50px auto;
  .content{
    padding: 40px;
    h1{
      margin: 0;
    }
    .card__wrapper{
      // display: grid;
      // grid-template-columns: 25% 25% 25% 25%;
      display: flex;
      flex-wrap: wrap;
    }
  }
}
header{
  border-bottom: 1px solid #eaeaea;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px;
  .headerLeft{
    display: flex;
    align-items: center;
  }
  

  img{
    margin-right: 15px;
  }

  h3, p{
    margin: 0;
  }

  .headerRight{
    list-style:none;
    li{
      margin-right: 30px;
      display: block;
      float: left;
    }
  }
}

.card{
  border: 1px solid #f2f2f2;
  padding: 20px;
  width: 210px;
  border-radius: 30px;
  padding: 30px;
  margin: auto;
  margin-top: 30px;
  transition: opacity 0.5s ease-in-out;
  &:hover{
    scale: 105%;
    box-shadow:0px 10px 20px rgba(0, 0, 0, 0.1);
    
  }
  span{
    text-transform: uppercase;
    opacity: 0.5;
    font-size: 13px;
  }
  b{
    font-size: 13px;
  }
  .card__info{
    display: flex;
    justify-content: space-between;
    .card__price{
      display: flex;
      flex-direction: column;
    }
  }
  button{
    background: #ffffff;
    border: 1px solid #f0ebeb;
    box-sizing: border-box;
    border-radius: 8px;
    width: 32px;
    height: 32px;
    cursor: pointer;
  }

  h5{
    font-weight: 600;
    font-size: 14px;
  }

  
}
.search{
  border: 1px solid #f2f2f2;
  display: flex;
  border-radius: 10px;
  padding: 0 15px;
}
.headContent{
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  justify-content: space-between;

  input{
    border: 0px;
    padding: 10px;
    font-size: 16px;
    font-family: "Inter";
    width: 200px;
  }
}
.unliked{
  position: absolute;
  width: 40px;
}
.liked{
  position: absolute;
  width: 40px;
}


.overlay{
  position:absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;
  width: 100%;
  height: 100%;
}
.drawer{
  position: absolute;
  width: 385px;
  height: 94%;
  right: 0;
  background: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.4);
  padding: 30px;
  display: flex;
  flex-direction: column;
  h2{
    margin: 0;
    font-size: 24px;
    display: flex;
    justify-content: space-between;
  }
}
.cart-item{
  display: flex;
  align-items: center;
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;
  margin-top: 20px;
  p{
    margin-bottom: 5;
    font-size: 14px;
  }
  b{
    font-size: 16px;
  }
  img{
    margin-right:20px ;
  }
  .remove-btn{
    opacity: 0.5;
    transition: opacity 0.2s ease-in-out
  }
  .remove-btn:hover{
    opacity: 1;
    
  }
}
.items{
  flex: 1;
  overflow: auto;
  margin-bottom: 40px;
}
.totalBlock{
  li{
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
  }
  ul{
    div{
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
  }
  button{
    width: 95%;
    /* Rectangle 25 */
    height: 55px;
    margin-left: 30px;
    background: #9DD558;
    border-radius: 18px;
    border: 0;
    color: #ffffff;
    font-size: 16px;
    cursor: pointer;
  }
}